import { Injectable } from '@angular/core';
import * as firebase from 'firebase/app';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { User } from './user';

@Injectable()
export class AuthService {

  user$: Observable<User>;
  member: any;
  memberDoc: any;
  member$: Observable<any>;
  project: any;
  projectDoc: any;
  project$: Observable<any>;

  constructor(private afAuth: AngularFireAuth,
              private afs: AngularFirestore) {

    firebase.auth().useDeviceLanguage()

      //// Get auth data, then get firestore user document || null
      this.user$ = this.afAuth.authState
        .switchMap(user => {
          if (user) {
            return this.afs.doc<any>(`users/${user.uid}`).valueChanges()
          } else {
            return Observable.of(null)
          }
        })
      this.user$.subscribe((user) => {
        if (user && user.username) {
          this.memberDoc = this.afs.doc<any>(`members/${user.username}`)
          this.member$ = this.memberDoc.valueChanges()
          this.member$.subscribe((member) => {
            if (!member) return console.error('No member found by username', user)
            this.member = member
            const project = member.project || member.projects[0].id
            this.projectDoc = this.afs.doc<any>(`projects/${project}`)
            this.project$ = this.projectDoc.valueChanges()
            this.project$.subscribe((project) => {
              console.log('Project', project)
              this.project = project
            })
          })
        } else console.error('No user found')
      })
  }

  openProject(id: string) {
    this.memberDoc.set({ project: id }, { merge: true })
  }

    ///// Login/Signup //////

  login({ email, password }) {
    return new Promise((resolve, reject) => {
      firebase.auth().signInWithEmailAndPassword(email, password)
        .then((user) => {
          console.log('user logged', user)
          this.updateUserData(user)
          return resolve(user)
        }).catch((error) => {
          console.error(error)
          return reject(error)
        })
      })
  }

  register({ username, email, password }) {
    return new Promise((resolve, reject) => {
      firebase.auth().createUserWithEmailAndPassword(email, password)
        .then((user) => {
          console.log('created user', user)
          user.username = username
          this.updateUserData(user)
          this.createMemberAccount(user)
          this.createWelcomeProject(user)
          return resolve(user)
        }).catch((error) => {
          console.error(error)
          return reject(error)
        })
    })
  }

  googleLogin() {
    const provider = new firebase.auth.GoogleAuthProvider()
    return this.oAuthLogin(provider);
  }

  private oAuthLogin(provider) {
    return this.afAuth.auth.signInWithPopup(provider)
      .then((credential) => {
        this.updateUserData(credential.user)
      })
  }

  signOut() {
    this.afAuth.auth.signOut()
  }

  private updateUserData(user: User) {
    // Sets user data to firestore on login
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(`users/${user.uid}`);
    const data = {
      email: user.email,
      username: user.username,
      platform: navigator.platform,
      since: new Date()
    }
    console.warn(userRef)
    return userRef.set(data, { merge: true })
  }

  private createMemberAccount(user: User) {
    const memberRef: AngularFirestoreDocument<any> = this.afs.doc(`members/${user.username}`);
    const data = {
      uid: user.uid,
      name: user.username,
      projects: [{
        id: 'vaxes-' + user.username,
        title: 'Vaxes Law'
      }]
    }
    console.warn(memberRef)
    return memberRef.set(data, { merge: true })
  }

  private createWelcomeProject(user: User) {
    const projectRef: AngularFirestoreDocument<any> = this.afs.doc(`projects/vaxes-${user.username}`);
    const data = {
      id: `vaxes-${user.username}`,
      title: 'Vaxes Law',
      description: `Bienvenido a bordo @${user.username}!`,
      admins: [],
      editors: { [user.uid]: true },
      activities: [{
        "messageId":"1",
        "userId":"210000198410281948",
        "userName":"Vaxes",
        "userImgUrl":"./assets/user.jpg",
        "toUserId":"210000198410281948",
        "toUserName":"Hancock",
        "userAvatar":"./assets/to-user.jpg",
        "time":1515087170242,
        "message":"Proyecto aceptado, @Sergio ha sido asignado a colaborar en el proyecto.",
        "status":"success"
      }]
    }
    console.warn('project', projectRef)
    projectRef.set(data, { merge: true })
    projectRef.set({ created: new Date() }, { merge: true })
    this.project$ = projectRef.valueChanges()
  }


  ///// Role-based Authorization //////

  canRead(user: User): boolean {
    const allowed = ['admin', 'editor', 'subscriber']
    return this.checkAuthorization(user, allowed)
  }

  canEdit(user: User): boolean {
    const allowed = ['admin', 'editor']
    return this.checkAuthorization(user, allowed)
  }

  canDelete(user: User): boolean {
    const allowed = ['admin']
    return this.checkAuthorization(user, allowed)
  }



  // determines if user has matching role
  private checkAuthorization(user: User, allowedRoles: string[]): boolean {
    if (!user) return false
    for (const role of allowedRoles) {
      if ( user.roles[role] ) {
        return true
      }
    }
    return false
  }


}



