import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavParams } from 'ionic-angular';
import { Events, Content, TextInput } from 'ionic-angular';
import { ChatService, ChatMessage, UserInfo } from "../../providers/chat-service";

import { AuthService } from '../../app/core/auth.service';
@IonicPage()
@Component({
    selector: 'page-chat',
    templateUrl: 'chat.html',
})
export class Chat {

    @ViewChild(Content) content: Content;
    @ViewChild('chat_input') messageInput: TextInput;
    msgList = [];
    user: UserInfo;
    toUser: UserInfo;
    editorMsg = '';
    showEmojiPicker = false;

    constructor(navParams: NavParams,
                public auth: AuthService,
                private chatService: ChatService,
                private events: Events,) {
        // Get the navParams toUserId parameter
        this.toUser = {
            id: navParams.get('toUserId'),
            name: navParams.get('toUserName')
        };
        // Get mock user information
        this.chatService.getUserInfo()
        .then((res) => {
            this.user = res
        });
    }

    ionViewWillLeave() {
        // unsubscribe
        this.events.unsubscribe('chat:received');
    }

    ionViewDidEnter() {
        //get message list
        this.getMsg();

        // Subscribe to received  new message events
        this.events.subscribe('chat:received', msg => {
            // this.pushNewMsg(msg);
        })
    }

    onFocus() {
        this.showEmojiPicker = false;
        this.content.resize();
        this.scrollToBottom();
    }

    /**
     * @name getMsg
     * @returns {Promise<ChatMessage[]>}
     */
    private getMsg() {
        // Get mock message list
        return this.chatService
        .getMsgList()
        .subscribe(res => {
            this.msgList = res;
            this.scrollToBottom();
        });
    }

    /**
     * @name sendMsg
     */
    sendMsg() {
        if (!this.editorMsg.trim()) return;

        let newMsg: ChatMessage = {
            user: this.auth.member.name,
            msg: this.editorMsg,
            time: Date.now()
        }

        if (this.auth.member.avatar) newMsg.userAvatar = this.auth.member.avatar

        this.editorMsg = '';

        if (!this.showEmojiPicker) {
            this.messageInput.setFocus();
        }
        this.auth.projectDoc.set({ activity: (this.auth.project.activity||[]).concat(newMsg) }, { merge: true })
          .then(() => {
            this.scrollToBottom()
          })
        //this.chatService.sendMsg(newMsg)
        // .then(() => {
        //     let index = this.getMsgIndexById(id);
        //     if (index !== -1) {
        //         this.msgList[index].status = 'success';
        //     }
        // })
    }

    getMsgIndexById(id: string) {
        return this.msgList.findIndex(e => e.messageId === id)
    }

    scrollToBottom() {
      if (this.content && this.content.scrollToBottom) {
          this.content.scrollToBottom();
      }
    }
}



