import { Injectable } from '@angular/core';
import { Events } from 'ionic-angular';
import { HttpClient } from "@angular/common/http";
import {Observable} from "rxjs/Observable";

import { AngularFirestore } from 'angularfire2/firestore';
import 'rxjs/add/observable/fromPromise';

export class ChatMessage {
    messageId: string;
    userId: string;
    userName: string;
    userAvatar: string;
    toUserId: string;
    toUserName: string;
    time: number | string;
    message: string;
    status: string;
    userImgUrl: string;
}

export class UserInfo {
    id: string;
    name?: string;
    avatar?: string;
}

@Injectable()
export class ChatService {

    chat: ChatMessage[] = [
        {
          "messageId":"1",
          "userId":"210000198410281948",
          "userName":"Vaxes",
          "userImgUrl":"./assets/user.jpg",
          "toUserId":"210000198410281948",
          "toUserName":"Hancock",
          "userAvatar":"./assets/to-user.jpg",
          "time":1515087170242,
          "message":"Proyecto aceptado, @Sergio ha sido asignado a colaborar en el proyecto.",
          "status":"success"

        },
        {
          "messageId":"2",
          "userId":"210000198410281948",
          "userName":"Sergio",
          "userImgUrl":"./assets/to-user.jpg",
          "toUserId":"140000198202211138",
          "toUserName":"Luff",
          "userAvatar":"./assets/user.jpg",
          "time":1491034800000,
          "message":"Buenas, deberíamos entrar en el detalle del Marco Legal vigente.",
          "status":"success"
        },
        {
          "messageId":"3",
          "userId":"140000198202211138",
          "userName":"Empresa",
          "userImgUrl":"./assets/user.jpg",
          "toUserId":"210000198410281948",
          "toUserName":"Hancock",
          "userAvatar":"./assets/to-user.jpg",
          "time":1515087420242,
          "message":"Claro! Hay algunos antecedentes que nos pueden orientar.",
          "status":"success"
        },
        {
          "messageId":"4",
          "userId":"210000198410281948",
          "userName":"Vaxes",
          "userImgUrl":"./assets/to-user.jpg",
          "toUserId":"140000198202211138",
          "toUserName":"Luff",
          "userAvatar":"./assets/user.jpg",
          "time":1515087470142,
          "message":"@Empresa ha subido un nuevo fichero #file1 llamado 'Contexto 2018'",
          "status":"success"
        },
        {
          "messageId":"5",
          "userId":"210000198410281948",
          "userName":"Sergio",
          "userImgUrl":"./assets/to-user.jpg",
          "toUserId":"140000198202211138",
          "toUserName":"Luff",
          "userAvatar":"./assets/user.jpg",
          "time":1515087470242,
          "message":"Perfecto, ahora nos ponemos manos a la obra!",
          "status":"success"
        }
      ]

    projectRef;
    project$;

    constructor(private http: HttpClient,
                private afs: AngularFirestore,
                private events: Events) {
      this.projectRef = this.afs.doc('projects/sSzSqYcEpI8UYDeuJm1R')
      this.project$ = this.projectRef.valueChanges()
      this.project$.subscribe(project => {
        console.log(project)
      })
    }

    mockNewMsg(msg) {
        const mockMsg: ChatMessage = {
            messageId: Date.now().toString(),
            userId: '210000198410281948',
            userName: 'Usuario',
            userAvatar: './assets/to-user.jpg',
            toUserId: '140000198202211138',
            time: Date.now(),
            message: msg.message,
            status: 'success',
            toUserName: '',
            userImgUrl: '',
        };

        setTimeout(() => {
            this.events.publish('chat:received', mockMsg, Date.now())
        }, Math.random() * 1800)
    }

    getMsgList(): Observable<ChatMessage[]> {
        if (this.chat) {
          return Observable.of(this.chat);
        } else {
          return this.http.get('assets/data/chat.json')
            .map((data: ChatMessage[]) => data);
        }
    }

    sendMsg(msg: ChatMessage) {
        return new Promise(resolve => setTimeout(() => resolve(msg), Math.random() * 1000))
        .then(() => this.mockNewMsg(msg));
    }

    getUserInfo(): Promise<UserInfo> {
        const userInfo: UserInfo = {
            id: '140000198202211138',
            name: 'Usuario',
            avatar: './assets/user.jpg'
        };
        return new Promise(resolve => resolve(userInfo));
    }

}



