import { Component, ViewChild } from '@angular/core';

import { Events, MenuController, Nav, Platform } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';

import { Storage } from '@ionic/storage';

import { AboutPage } from '../pages/about/about';
import { AccountPage } from '../pages/account/account';
import { LoginPage } from '../pages/login/login';
import { SignupPage } from '../pages/signup/signup';
import { TabsPage } from '../pages/tabs-page/tabs-page';
import { TutorialPage } from '../pages/tutorial/tutorial';
import { SchedulePage } from '../pages/schedule/schedule';
import { SpeakerListPage } from '../pages/speaker-list/speaker-list';
import { SupportPage } from '../pages/support/support';

import { ConferenceData } from '../providers/conference-data';
import { UserData } from '../providers/user-data';
import { AuthService } from './core/auth.service';

// import { AdminGuard } from './core/admin.guard';
// import { CanReadGuard } from './core/can-read.guard';

export interface PageInterface {
  title: string;
  name: string;
  component: any;
  icon: string;
  logsOut?: boolean;
  index?: number;
  tabName?: string;
  tabComponent?: any;
  url?: string;
}

@Component({
  templateUrl: 'app.template.html'
})
export class ConferenceApp {
  // the root nav is a child of the root app component
  // @ViewChild(Nav) gets a reference to the app's root nav
  @ViewChild(Nav) nav: Nav;

  // List of pages that can be navigated to from the left menu
  // the left menu only works after login
  // the login page disables the left menu
  projects: PageInterface[] = [
    { title: 'Asesoría legal', name: 'TabsPage', component: TabsPage, tabComponent: AboutPage, index: 0, icon: 'briefcase' },
    { title: 'Apuestas online', name: 'TabsPage', component: TabsPage, tabComponent: AboutPage, index: 0, icon: 'briefcase' }
  ];
  contacts: PageInterface[] = [
    { title: 'Sergio', name: 'TabsPage', component: TabsPage, tabComponent: SchedulePage, index: 0, icon: 'contact' },
    { title: 'Xavi', name: 'TabsPage', component: TabsPage, tabComponent: SpeakerListPage, index: 1, icon: 'contact' }
  ];
  loggedInPages: PageInterface[] = [
    { title: 'Nuevo Proyecto', name: 'SupportPage', component: SupportPage, icon: 'bookmarks' },
    { title: 'Mi Perfil', name: 'AccountPage', component: AccountPage, icon: 'person' },
    { title: 'Salir', name: 'TabsPage', component: LoginPage, icon: 'log-out', logsOut: true }
  ];
  loggedOutPages: PageInterface[] = [
    { title: 'Acceder', name: 'LoginPage', component: LoginPage, icon: 'log-in' },
    { title: 'Registro', name: 'SignupPage', component: SignupPage, icon: 'person-add' },
    { title: 'Abogados', name: 'TabsPage', component: TabsPage, icon: 'person', url: 'https://vaxeslaw.com/trabaja-con-nosotros/' },
    { title: 'Empresas', name: 'SupportPage', component: SupportPage, icon: 'briefcase' }
  ];
  rootPage: any;

  constructor(
    public events: Events,
    public userData: UserData,
    public menu: MenuController,
    public platform: Platform,
    public confData: ConferenceData,
    public storage: Storage,
    public auth: AuthService,
    public splashScreen: SplashScreen
  ) {

    // Check if the user has already seen the tutorial
    this.storage.get('hasSeenTutorial')
      .then((hasSeenTutorial) => {
        if (hasSeenTutorial) {
          this.userData.hasLoggedIn().then((hasLoggedIn) => {
            this.enableMenu(hasLoggedIn === true);
            this.rootPage = hasLoggedIn === true ? TabsPage : LoginPage;
          });
          this.enableMenu(true);
        } else {
          this.rootPage = TutorialPage;
        }
        this.platformReady()
      });

    // load the conference data
    confData.load();

    this.listenToLoginEvents();
  }

  openProject({ id, title }) {
    this.auth.openProject(id)
    return this.openPage({ title, name: 'TabsPage', projectId: id, component: TabsPage, tabComponent: AboutPage, index: 0, icon: 'briefcase' })
  }

  openPage(page: PageInterface) {
    if (page.url) {
      return window.open(page.url, '_blank');
    }
    let params = {};

    if (page.index) params.tabIndex = page.index
    if (page.projectId) params.projectId = page.projectId

    // If we are already on tabs just change the selected tab
    // don't setRoot again, this maintains the history stack of the
    // tabs even if changing them from the menu
    if (this.nav.getActiveChildNavs().length && page.index != undefined) {
      this.nav.getActiveChildNavs()[0].select(page.index);
    } else {
      // Set the root of the nav with params if it's a tab index
      this.nav.setRoot(page.name, params).catch((err: any) => {
        console.log(`Didn't set nav root: ${err}`);
      });
    }

    if (page.logsOut === true) {
      // Give the menu time to close before changing to logged out
      this.userData.logout();
    }
  }

  openTutorial() {
    this.nav.setRoot(TutorialPage);
  }

  listenToLoginEvents() {
    this.events.subscribe('user:login', () => {
      this.enableMenu(true);
    });

    this.events.subscribe('user:signup', () => {
      this.enableMenu(true);
    });

    this.events.subscribe('user:logout', () => {
      this.enableMenu(false);
    });
  }

  enableMenu(loggedIn: boolean) {
    this.menu.enable(loggedIn, 'loggedInMenu');
    this.menu.enable(!loggedIn, 'loggedOutMenu');
  }

  platformReady() {
    // Call any initial plugins when ready
    this.platform.ready().then(() => {
      this.splashScreen.hide();
    });
  }

  isSelected(page: PageInterface) {
    return !page
  }

  isActive(page: PageInterface) {
    let childNav = this.nav.getActiveChildNavs()[0];

    // Tabs are a special case because they have their own navigation
    if (childNav) {
      if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
        return 'primary';
      }
      return;
    }

    if (this.nav.getActive() && this.nav.getActive().name === page.name) {
      return 'primary';
    }
    return;
  }
}



